.header {
  max-height: var(--header-height);
  padding: var(--header-padding);
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 30px;
}

.nav__list {
  display: flex;
  align-items: center;
  column-gap: 40px;
}

.nav__list-item {
  font-size: 21px;
  font-weight: 400;
  letter-spacing: 0.14em;
  line-height: 226%;
  position: relative;
}

.nav__list-item_button {
  /* font-size: 21px;
  font-weight: 400;
  letter-spacing: 0.14em;
  line-height: 226%; */
  /* cursor: pointer;
  position: relative;
  text-decoration: none; */
}

.nav__list-item::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: black;
  transition: width 0.3s ease;
}

.nav__list-item:hover::before {
  width: 100%;
}

.nav__list-item_panel {
  font-size: 18px;
  font-weight: 400;
  line-height: 40.68px;
  letter-spacing: 0.14em;
  width: 395px;
}

.title {
  /* margin: 0 40px 0 0;
  padding: 0 0 15px 0;
   */
}

.links__count {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 285px;
}

.links__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 37px;
  height: 37px;
  position: relative;
  padding: 0;
  cursor: pointer;
  border: none;
}

.links__button:hover {
  box-shadow: none;
}

.links__button_img {
  height: var(--default-icon-size);
  width: var(--default-icon-size);
}

.links__button::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: black;
  transition: width 0.3s ease;
}

.links__button:hover::before {
  width: 100%;
}

