/* Media Query for Mobile View */
@media (max-width: 992px) {
  .nav-bar__count {
    display: none;
  }

  .burger-menu {
    display: block;
  }

  .header {
    justify-content: space-between;
  }

  .links__count {
    display: none;
  }
}
